* {
  padding: 0;
  margin: 0;
}
body {
  font-family: 'Nunito', sans-serif;
}
ul {
  list-style: none;
}
/* Main div */
.main-div {
  display: flex;
  box-sizing: border-box;
}
.column {
  flex-grow: 1;
}

/* Side Menu */
.side-menu {
  background: #1c2833;
  height: 100vh;
  overflow: auto;
}
.side-menu .header {
  color: #fff;
  text-align: center;
  padding: 30px 0;
  font-size: 2rem;
}
.side-menu hr {
  background-color: #566573;
  height: 1px;
  margin: 10px 0;
  border: none;
}
.side-menu ul li {
  padding: 20px 10px;
  padding-right: 150px;
  font-weight: 10;
  font-size: 1.2rem;
  color: #566573;
}
.side-menu ul li:hover {
  color: #d5d8dc;
  background: #17202a;
}
.side-menu ul li i {
  padding-right: 10px;
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background: #3c8dbc;
  color: #efebe9;
}
.navbar h1 {
  margin-right: auto;
  font-family: 'Nunito', sans-serif;
  color: #fff;
  font-size: 2rem;
  padding-left: 10px;
}
.navbar ul li {
  display: inline-block;
  padding: 20px 30px;
  border-radius: 20px;
}

.navbar ul li:hover {
  background-color: #23527c;
  color: #d9dddf;
}
